<template>
  <div class="recommend" ref="recommend">
    <scroll ref="scroll" class="recommend-content" :data="diskList">
      <div>
        <div class="slider-wrapper" v-if="recommends.length" ref="sliderWrapper">
          <slider>
            <div v-for="item in recommends">
              <a :href="item.linkUrl">
                <img :src="item.picUrl" alt="">
              </a>
            </div>
          </slider>
        </div>
        <div class="recommend-list">
          <h1 class="list-title">热门歌单推荐</h1>
          <ul>
            <li v-for="item in diskList" class="item" @click="selectItem(item)">
              <div class="icon">
                <img @load="loadImage" v-lazy="item.imgurl" alt="" width="60" height="60">
              </div>
              <div class="text">
                <h2 class="name" v-html="item.creator.name"></h2>
                <p class="desc" v-html="item.dissname"></p>
              </div>
            </li>
          </ul>
        </div>
      </div>
      <div class="loading-container" v-show="!diskList.length">
        <loading></loading>
      </div>
    </scroll>
    <router-view></router-view>
  </div>
</template>

<script type="text/ecmascript-6">
  import Slider from '@/base/slider/slider'
  import {getRecommend, getDiscList} from "api/recommend";
  import {ERR_OK} from "api/config";
  import Scroll from '@/base/scroll/scroll'
  import Loading from '@/base/loading/loading'
  import {playlistMixin} from "common/js/mixin";
  import {mapMutations} from 'vuex'


  export default {
    mixins:[playlistMixin],
    data() {
      return {
        recommends: [],
        diskList: []
      }
    },
    created() {
      this._getRecommend()  //获取slider
      setTimeout(()=>{
        this._getDiscList()   //获取歌单
      },1000)

    },
    methods: {
      ...mapMutations({
        setDisc:'SET_DISC'
      }),
      handlePlaylist(playlist){  //有playlist的时候更新
        const bottom = playlist.length > 0 ? '60px' : ''
        this.$refs.recommend.style.bottom = bottom
        this.$refs.scroll.refresh()
      },
      _getRecommend() {     //获取slider数据
        getRecommend().then((res) => {
          if (res.code === ERR_OK) {
            // console.log(res)
            this.recommends = res.data.slider
          }
        })
      },
      _getDiscList() {     //获取歌单列表
        getDiscList().then((res) => {
          // console.log(res)
          this.diskList=res.data.list

        })
      },
      loadImage(){    //确保slider图片加载完成后调用scroll.refresh()方法
        if (!this.checkLoaded){   //设置标志位确定只执行一次
          this.$refs.scroll.refresh()
          this.checkLoaded=true
        }
      },
      selectItem(item){  //跳转路由，传递数据给vuex的disc
        this.$router.push({path: `/recommend/${item.dissid}`})
        this.setDisc(item)
      }
    },
    components: {
      Slider,
      Scroll,
      Loading

    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~@/common/stylus/variable"

  .recommend
    position: fixed
    width: 100%
    top: 88px
    bottom: 0
    .recommend-content
      height: 100%
      overflow: hidden
      .slider-wrapper
        position: relative
        width: 100%
        overflow: hidden
      .recommend-list
        .list-title
          height: 65px
          line-height: 65px
          text-align: center
          font-size: $font-size-medium
          color: $color-theme
        .item
          display: flex
          box-sizing: border-box
          align-items: center
          padding: 0 20px 20px 20px
          .icon
            flex: 0 0 60px
            width: 60px
            padding-right: 20px
          .text
            display: flex
            flex-direction: column
            justify-content: center
            flex: 1
            line-height: 20px
            overflow: hidden
            font-size: $font-size-medium
            .name
              margin-bottom: 10px
              color: $color-text
            .desc
              color: $color-text-d
      .loading-container
        position: absolute
        width: 100%
        top: 50%
        transform: translateY(-50%)
</style>



